<template>
  <div>
    <div class="wrapper">
      <div class="stats-container">
        <div id="moves-count" ref="moves">{{ movesCount }}</div>
        <div id="time" ref="timeValue"></div>
      </div>
      <div ref="gameContainer" class="game-container">
        <div v-for="(card, index) in cards" :key="index" class="card-container" :data-card-value="card.value">
          <input type="hidden" :value="card.image">
          <div class="card-before">?</div>
          <div class="card-after" :style="{ 'background-color': card.image}">
            <!-- <img :src="card.image" class="image" :alt="card.value" style="object-fit: contain;"> -->
          </div>
        </div>
      </div>
      <button ref="stopButton" id="stop" class="hide" @click="stopGame">Stop Game</button>
    </div>
    <div ref="controls" class="controls-container">
      <p id="result" ref="result"></p>
      <button ref="startButton" id="start" @click="startGame">Start Game</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      movesCount: 0,
      cards: [],
      interval: null,
      firstCard: null,
      secondCard: null,
      firstCardValue: null,
      secondCardValue: null,
      items: [
        { name: "bee", image: "red" },
        { name: "crocodile", image: "orange" },
        { name: "macaw", image: "yellow" },
        { name: "gorilla", image: "green" },
        { name: "tiger", image: "blue" },
        { name: "monkey", image: "violet" },
        { name: "chameleon", image: "black" },
        { name: "piranha", image: "gray" },
        { name: "anaconda", image: "lime" },
        { name: "sloth", image: "olive" },
        { name: "cockatoo", image: "indigo" },
        { name: "toucan", image: "orangered" },
      ],
      seconds: 0,
      minutes: 0,
      winCount: 0,
    };
  },
  methods: {
    timeGenerator() {
      this.seconds += 1;
      // minutes logic
      if (this.seconds >= 60) {
        this.minutes += 1;
        this.seconds = 0;
      }
      // format time before displaying
      let secondsValue = this.seconds < 10 ? `0${this.seconds}` : this.seconds;
      let minutesValue = this.minutes < 10 ? `0${this.minutes}` : this.minutes;
      this.$refs.timeValue.innerHTML = `<span>Time:</span>${minutesValue}:${secondsValue}`;
    },
    movesCounter() {
      this.movesCount += 1;
      this.$refs.moves.innerHTML = `<span>Moves:</span>${this.movesCount}`;
    },
    generateRandom(size) {
      // temporary array
      let tempArray = [...this.items];
      // initializes cardValues array
      let cardValues = [];
      // size should be double (4*4 matrix)/2 since pairs of objects would exist
      size = (size * size) / 2;
      // Random object selection
      for (let i = 0; i < size; i++) {
        const randomIndex = Math.floor(Math.random() * tempArray.length);
        cardValues.push(tempArray[randomIndex]);
        // once selected remove the object from temp array
        tempArray.splice(randomIndex, 1);
      }
      return cardValues;
    },
    matrixGenerator(cardValues, size) {
      // this.$refs.gameContainer.innerHTML = "";
      this.cards = [];
      cardValues = [...cardValues, ...cardValues];
      // simple shuffle
      cardValues.sort(() => Math.random() - 0.5);
      for (let i = 0; i < size * size; i++) {
      console.log("card"+cardValues[i].name);
      
        this.cards.push({
          value: cardValues[i].name,
          image: cardValues[i].image,
          flipped: false,
          matched: false,
        });
      }

      console.log('this.cards: ',this.cards);
      // Grid
      this.$refs.gameContainer.style.gridTemplateColumns = `repeat(${size},auto)`;
      // Cards
      const cards = document.querySelectorAll(".card-container");
      console.log(cards);
      cards.forEach((card) => {
        card.addEventListener("click", () => {
          // If selected card is not matched yet then only run (i.e already matched card when clicked would be ignored)
          if (!card.classList.contains("matched")) {
            // if(!card.matched){
            // flip the clicked card
            card.classList.add("flipped");
            // this.$set(card, 'flipped', true);
            console.log(card);
            // card.flipped = true;
            // console.log("this is a card: ", card);
            // if it is the first card (!firstCard since firstCard is initially false)
            if (!this.firstCard) {
              // so current card will become firstCard
              this.firstCard = card;
              // current card's value becomes firstCardValue
              this.firstCardValue = card.getAttribute("data-card-value");
            } else {
              // increment moves since user selected second card
              this.movesCounter();
              // secondCard and value
              this.secondCard = card;
              let secondCardValue = card.getAttribute("data-card-value");
              if (this.firstCardValue == secondCardValue) {
                // if both cards match add matched class so these cards would be ignored next time
                this.firstCard.classList.add("matched");
                this.secondCard.classList.add("matched");
                // set firstCard to false since the next card would be first now
                this.firstCard = false;
                // winCount increment as the user found a correct match
                this.winCount += 1;
                // check if winCount == half of cardValues
                if (this.winCount == Math.floor(cardValues.length / 2)) {
                  this.$refs.result.innerHTML = `<h2>You Won</h2>
                    <h4>Moves: ${this.movesCount}</h4>`;
                  this.stopGame();
                }
              } else {
                // if the cards don't match
                // flip the cards back to normal
                let [tempFirst, tempSecond] = [this.firstCard, this.secondCard];
                this.firstCard = false;
                this.secondCard = false;
                let delay = setTimeout(() => {
                  tempFirst.classList.remove("flipped");
                  tempSecond.classList.remove("flipped");
                }, 900);
              }
            }
          }
        });
      });
    },
    startGame() {
      this.movesCount = 0;
      this.seconds = 0;
      this.minutes = 0;
      // controls and buttons visibility
      this.$refs.controls.classList.add("hide");
      this.$refs.stopButton.classList.remove("hide");
      this.$refs.startButton.classList.add("hide");
      
      
      // Start timer
      this.interval = setInterval(this.timeGenerator, 1000);
      // initial moves
      this.$refs.moves.innerHTML = `<span>Moves:</span> ${this.movesCount}`;
      this.initializer();
    },
    stopGame() {
      this.$refs.controls.classList.remove("hide");
      this.$refs.stopButton.classList.add("hide");
      this.$refs.startButton.classList.remove("hide");
      // const cards = document.querySelectorAll(".card-container");
      // console.log(cards);
      // this.cards.flipped = false;
      // this.cards.matched = false;
      clearInterval(this.interval);
      setTimeout(window.location.reload(), 5000);
    },
    initializer() {
      this.$refs.result.innerText = "";
      this.winCount = 0;
      const size = 4;
      let cardValues = this.generateRandom(size);
      console.log(cardValues);
      for(let i = 0; i < cardValues.length; i++){
        console.log('card: '+cardValues[i].name);
      }
      this.matrixGenerator(cardValues, size);
    },
  },
  mounted() {
    // Call your initializer on component mount or as needed
    this.$nextTick(() => {
      this.initializer();
    });
  },
    };
    </script>
  
